<template>
  <div class="song">
    <van-tabs v-model="active" :sticky="true" offset-top="1.23rem">
      <van-tab title="综合"
        ><ul>
          <SongItem
            v-for="(song, index) in searchResult"
            :key="index"
            :song="song"
            :index="index"
            @list-update-playlist="updatePl"
          ></SongItem></ul
      ></van-tab>
      <van-tab title="歌手">
        <div class="artists">
          <ul>
            <li
              v-for="artist in searchArtists"
              :key="artist.id"
              @click="navigate(artist.id,artist.img1v1Url)"
            >
              <div class="pic">
                <img
                  :src="artist.img1v1Url ? artist.img1v1Url : artist.picUrl"
                />
              </div>
              <div class="name">{{ artist.name }}</div>
            </li>
          </ul>
        </div>
      </van-tab>
    </van-tabs>
  </div>
</template>

<script>
import SongItem from "@/components/SongItem.vue";

export default {
  name: "Song",
  components: {
    SongItem,
  },
  data() {
    return {
      searchResult: [],
      active: 0,
      searchArtists: [],
    };
  },
  watch: {
    active(n) {
      if (n == 1) {
        // console.log("beforeRouteEnter");
        this.$toast.loading({
          message: "加载中...",
          forbidClick: true,
          duration: 0,
        });

        this.axios
          .get(`/search?keywords=${this.$route.query.name}&type=100`)
          .then((response) => {
            // console.log(response.data.result);
            this.searchArtists = response.data.result.artists;

            this.$toast.clear();
          })
          .catch(() => {
            this.$toast.clear();
          });
      }
    },
  },
  activated() {
    console.log("activated");
    this.active = 0;

    this.$toast.loading({
      message: "加载中...",
      forbidClick: true,
      duration: 0,
    });
    this.axios
      .get(`/search?keywords=${this.$route.query.name}`)
      .then((response) => {
        // console.log(response.data.result.songs);
        var sResult = response.data.result.songs;

        this.searchResult = sResult.map((element) => {
          if (!element.song) {
            element.song = {};
            element.song.artists = element.artists;
          }
          element.picUrl = element.album.artist.img1v1Url;
          return element;
        });

        this.$store.commit("updateSearchHistory", this.$route.query.name);

        this.$toast.clear();
      })
      .catch(() => {
        this.$toast.clear();
      });
  },
  methods: {
    updatePl() {
      this.$store.commit("updatePlaylist", this.searchResult);
    },
    navigate(id,img) {
      this.$emit('update:image',img)
      this.$router.push({ name: "Artist", query: { id: id} });
    },
  },
};
</script>

<style lang="less" scoped>
.song {
  width: 100vw;
  height: calc(100vh - 96px);
  margin-top: 46px;
  background-color: #fff;
  position: fixed;
  z-index: 1000;
  overflow-x: scroll;
  .artists {
    margin-top: 10px;
    ul {
      li {
        display: flex;
        align-items: center;
        margin: 20px 10px;
        &:first-of-type {
          margin-top: 10px;
        }
        .pic {
          height: 50px;
          width: 50px;
          border-radius: 50%;
          overflow: hidden;
          img {
            display: block;
            width: 100%;
          }
        }
        .name {
          margin-left: 15px;
        }
      }
    }
  }
}
</style>